.con{
    width: 100%;
    height: 100vh;
    position: relative;
}
.con img{
    width: 100%;
    height: 100%;
}
.circon{
    position: absolute;
    color: crimson;
    width: 100%;
    bottom: 50px;
    text-align: center;
    letter-spacing: 7px;
}
.fa-circle{
    color: crimson;
    
}
.left{
    top: 50%;
    left: 50px;
    position: absolute;
    color: crimson;
    font-size: 50px;
    font-weight: bolder;
}
.right{
    top: 50%;
    position: absolute;
    right: 50px;
    color: crimson;
    font-size: 50px;
    font-weight: bolder;
}
